@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;500;600;700&display=swap);
:root{--main-clr: #fffdfa;--font-clr: #1B1B1B;--font-clr-lite: #707070;--dark-clr: #1B1B1B;--border-clr: #d1d1d1;--font-normal: 1.2rem;--heading-second: 5rem;--font-15: 1.5rem;--font-medium: 1.6rem;--font-serif: 'Cirka';--section-margin-top: 18rem}*{margin:0;padding:0;box-sizing:inherit}*:focus{outline:none;border:none}*::before,*::after{box-sizing:inherit}.row{max-width:114rem;margin:0 auto}html{box-sizing:border-box;font-size:62.5%}@font-face{font-family:'Cirka';src:url(/src/fonts/Cirka-Bold.woff);font-style:normal;font-weight:700}@font-face{font-family:'Cirka';src:url(/src/fonts/Cirka-Light.otf);font-style:normal;font-weight:400}body{font-family:'Public Sans', sans-serif;color:#1B1B1B;color:var(--font-clr);font-weight:400}


.alert{width:50vw;height:50vh;background-color:var(--dark-clr);position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);z-index:200;text-align:center;color:var(--main-clr);padding:2rem}.alert>p{font-size:2rem}main{margin-top:7.6rem}

nav{box-shadow:0 0.1rem 5rem rgba(0,0,0,0.1);position:fixed;top:0;left:0;width:100%;z-index:70}

.navigation-items{display:flex;justify-content:space-between;align-items:center;list-style:none;background-color:var(--main-clr)}.navigation-items__navigation-item:first-child{position:relative}.navigation-items__navigation-item:first-child::before{content:'';width:2rem;height:2px;background-color:var(--dark-clr);position:absolute;top:42%;left:50%;transform:translateX(-50%)}.navigation-items__navigation-item:first-child::after{content:'';width:2rem;height:2px;background-color:var(--dark-clr);position:absolute;bottom:42%;left:50%;transform:translateX(-50%)}.navigation-items__navigation-item:nth-child(2){font-family:cirka;font-weight:700;font-size:3rem;text-transform:uppercase;padding:2rem;letter-spacing:8px}.navigation-items__navigation-item{padding:3rem;font-weight:700;font-size:1.3rem;text-transform:uppercase}.navigation-items__navigation-item>svg{stroke-width:2rem}

.Sidenav{position:fixed;background:var(--dark-clr);width:30%;height:100%;z-index:100;top:0px;transform:translateX(-102%);transition:transform 400ms cubic-bezier(0, 0, 0.71, 1.04);padding:2rem;visibility:visible}.Sidenav.active{visibility:visible;transform:translateX(0%)}.Sidenav__signup-box{display:flex;justify-content:space-between;align-items:center;padding-bottom:5rem}.Sidenav__signup-box>div:last-child{width:4rem;height:4rem;color:var(--main-clr)}.Sidenav__signup-box>div:last-child>svg{height:100%;width:100%}.Sidenav__links{list-style:none}.Sidenav__link{position:relative}.Sidenav__link::before{content:"";height:1px;width:100%;background-color:var(--main-clr);position:absolute;bottom:0;left:0;transform:scaleX(0);transform-origin:center;transition:transform .2s linear}.Sidenav__link:hover::before{transform:scaleX(1)}.Sidenav__link>a{font-size:1.4rem;text-transform:uppercase;color:var(--main-clr);text-decoration:none;display:block;padding:1.5rem}

.btn{color:var(--font-clr);font-family:'Public Sans';font-weight:700;font-size:1.1rem;text-transform:uppercase;letter-spacing:0.1rem;background:none;outline:none;padding:1.8rem 5.3rem;border-radius:50px;cursor:pointer}.btn.btn-thin{border:1px solid var(--border-clr)}.btn.btn-fill{border:none;color:white;background-color:var(--dark-clr)}.btn.btn-fill-white{border:none;color:var(--dark-clr);background-color:var(--main-clr)}.btn.btn-thin-continue-shopping{padding:1.6rem 3rem;border:1px solid var(--border-clr)}.btn.btn-fill-checkout{color:white;background-color:var(--dark-clr);padding:1.8rem 6.2rem;border:1px solid var(--border-clr)}.btn.btn-fill-cart{width:100%;padding:1.6rem 5.3rem;border:none;color:white;background-color:var(--dark-clr)}.btn.btn-thin-cart{width:100%;padding:1.6rem 5.3rem;border:1px solid var(--border-clr)}

.footer{padding:10rem 0;border-top:1px solid #e4e4e4;border-bottom:1px solid #e4e4e4}.footer__inner{display:flex;justify-content:space-between;width:78%}.footer__lists{list-style:none}.footer__lists-heading{font-size:1.6rem;margin-bottom:4rem;text-transform:capitalize}.footer__list{font-size:1.4rem;text-transform:capitalize;line-height:3rem;font-weight:400}.footer__list>a{color:var(--font-clr-lite);text-decoration:none}.footer-last__inner{display:flex;font-size:var(--font-normal);padding:2.5rem 0rem}.footer-last__inner>p{font-weight:400;color:var(--font-clr-lite);margin:0 auto 0 0}.footer-last__inner>div{display:flex;align-items:center}.footer-last__inner>div>p{font-weight:700;text-transform:capitalize;padding-right:12rem}.footer-last__inner>div>div{font-size:1.9rem;padding-top:0.25rem}.footer-last__inner>div>div>svg{stroke-width:0.4px}

.modal{display:none;position:fixed;top:0;left:0;height:100vh;width:100vw;background-color:black;z-index:90;opacity:0}.modal.active{display:block;opacity:0.6}

.Cart-Dropdown{width:32rem;height:60rem;position:fixed;top:7.6rem;right:0;z-index:80;background-color:white;box-shadow:0px 1px 12px rgba(0,0,0,0.1);padding:2rem;overflow-y:auto}.Cart-Dropdown__inner .Cart-Dropdown__item{display:flex;justify-content:space-between;align-items:center;margin:3rem 2rem;padding-bottom:3rem;border-bottom:1px solid #dedede}.Cart-Dropdown__inner .Cart-Dropdown__item:last-child{border:none}.Cart-Dropdown__inner .Cart-Dropdown__item .Cart-Dropdown__content{flex:1 0 75%;display:flex}.Cart-Dropdown__inner .Cart-Dropdown__item .Cart-Dropdown__content .Cart-Dropdown__dlt-icon{font-size:2rem;padding-right:1.5rem}.Cart-Dropdown__inner .Cart-Dropdown__item .Cart-Dropdown__content .Cart-Dropdown__summery h4{width:70%;font-family:'Cirka';font-size:1.6rem;text-transform:capitalize;padding-bottom:1rem}.Cart-Dropdown__inner .Cart-Dropdown__item .Cart-Dropdown__photo{flex:1 0 25%}.Cart-Dropdown__inner .Cart-Dropdown__item .Cart-Dropdown__photo>img{width:100%}.Cart-Dropdown__btn .Cart-Dropdown__view-cart{margin-bottom:2rem}

.hero-section{width:100%}.hero-section__inner{display:flex;justify-content:space-between;width:100%;height:67rem}.hero-section__photos{flex:0 1 39%;position:relative;overflow:hidden}.hero-section__photos .slide-counter{position:absolute;top:2rem;right:2rem;font-weight:500;color:white}.hero-section__photos .slide-counter .current_slide{font-size:2rem;font-weight:700}.hero-section__photos .slide-counter .total_slide{font-size:1.5rem;font-weight:400}.hero-section__photos-inner{display:flex;width:100%;height:100%;transition:all 0.7s cubic-bezier(0, 0, 0.71, 1.04)}.hero-section__photos-inner>div{flex:1 0 100%}.hero-section__photos-inner>div>img{width:100%}.hero-section__photo{width:100%;height:auto;margin-top:-5rem}.hero-section__content{height:100%;flex:0 1 58%;align-self:flex-start}.hero-section__content-inner{display:flex;flex-direction:column;justify-content:space-between;height:100%;padding-left:11rem}.hero-section__content-headings{font-size:12rem;font-family:var(--font-serif);font-weight:700;text-transform:capitalize;line-height:11rem;padding:5rem 0rem 4rem 0rem}.hero-section__content-para{font-size:var(--font-medium);font-weight:400;padding-bottom:4rem}.hero-section__bottom{display:flex;align-items:center;margin-top:8.6rem}.hero-section__bottom .slide-controller{flex-basis:40%;display:flex}.hero-section__bottom .slide-controller>div{display:flex;justify-content:center;align-items:center;margin-right:1rem}.hero-section__bottom .article-box{display:flex;align-items:center;flex-basis:60%;background-color:var(--dark-clr);height:auto;padding:1rem}.hero-section__bottom .article-box .article-photo{width:26.5%;overflow:hidden}.hero-section__bottom .article-box .article-photo img{width:100%;height:auto}.hero-section__bottom .article-box .article-content{text-align:left;color:var(--main-clr);line-height:2rem;margin-left:2rem;margin-right:2rem;padding:0 2rem}.hero-section__bottom .article-box .article-content h4{letter-spacing:1px;text-transform:uppercase;font-size:1.1rem;font-weight:500;padding-bottom:1rem}.hero-section__bottom .article-box .article-content p{font-size:1.6rem}

.arrow-btn{display:flex;justify-content:center;align-items:center;width:4rem;height:4rem;border:1px solid var(--border-clr);font-size:1.5rem;border-radius:100%;transition:all 300ms}.arrow-btn:hover{color:white;background-color:var(--dark-clr)}

.exclusive_collection{margin-top:var(--section-margin-top);margin-bottom:var(--section-margin-top)}.exclusive_collection__container-inner{background-color:var(--main-clr);display:flex;justify-content:space-between;align-items:flex-start}.exclusive_collection__container-inner>div{flex:0 1 50%}.exclusive_collection__left>div{background-color:#707070;width:76%;height:60rem;overflow:hidden}.exclusive_collection__left-photo{width:100%;height:auto}.exclusive_collection__right{position:relative}.exclusive_collection__right>div:nth-child(2){width:60%;height:48rem;background-color:var(--dark-clr);overflow:hidden}.exclusive_collection__right>div:last-child{transform:rotate(-90deg);position:absolute;top:66.5%;right:-7%;text-transform:uppercase;text-align:center;font-size:var(--font-normal)}.exclusive_collection__right>div:last-child>p:first-child{font-weight:500;font-size:1rem;letter-spacing:1px}.exclusive_collection__right>div:last-child>p:last-child{padding-top:1rem;font-weight:700}.exclusive_collection__right-photo{width:100%;height:auto}.exclusive_collection__right-para{margin-top:2rem;color:var(--font-clr-lite);font-size:var(--font-15);font-weight:400;word-spacing:2px;line-height:2.3rem}.exclusive_collection__right-para>p:not(:first-child){margin-top:2rem;margin-bottom:12rem}

.heading-second{font-size:var(--heading-second);font-family:var(--font-serif);text-transform:capitalize;padding-right:15rem;margin-bottom:11rem;position:relative}.heading-second::after{content:" ";display:inline-block;width:108px;position:absolute;top:120%;left:0%;border-bottom:1px solid var(--dark-clr)}

.section-promote{position:relative;margin-bottom:var(--section-margin-top)}.section-promote__left-arrow{position:absolute;top:43.5%;margin-left:-9rem}.section-promote__right-arrow{position:absolute;top:43.5%;right:0%;margin-right:-9rem}.section-promote__heading{display:flex;align-items:flex-start}.section-promote__heading>div:first-child{width:40%;margin:0 auto 0 0}.section-promote__heading>div:last-child{margin-top:1rem}.section-promote__container-gallery{position:relative}.section-promote__container-gallery>div:last-child{width:100%;height:auto;overflow:hidden}.section-promote__gallery-inner{width:100%;height:100%;display:flex;justify-content:flex-start;transition:all 0.8s cubic-bezier(0, 0, 0.71, 1.04);will-change:transform}.section-promote__gallery-photo{flex:1 0 33.34%;height:35rem;overflow:hidden}.section-promote__gallery-photo img{width:100%}

.style-feeds{margin-bottom:var(--section-margin-top)}.style-feeds__heading{display:flex}.style-feeds__heading>div:first-child{margin:0 auto 0 0}.style-feeds__heading>div:last-child{padding-top:1rem}.style-feeds__gallery-photos{position:relative}.style-feeds__gallery-photos>div:last-child{width:100%;height:auto;overflow:hidden}.style-feeds__left-arrow{position:absolute;top:35%;margin-left:-9rem}.style-feeds__right-arrow{position:absolute;top:35%;right:0%;margin-right:-9rem}.style-feeds__gallery-photos-inner{width:100%;height:100%;display:flex;justify-content:flex-start;transition:all 0.8s cubic-bezier(0, 0, 0.71, 1.04);will-change:transform}.style-feeds__gallery-photo-box{flex:1 0 30%;display:flex;flex-direction:column;align-items:center}.style-feeds__gallery-photo-box:not(:last-child){margin-right:5%}.style-feeds__gallery-photo{width:100%;height:45rem;background-color:var(--font-clr-lite);overflow:hidden}.style-feeds__gallery-photo>img{width:110%}.style-feeds__gallery-content{text-align:center;width:60%;margin-top:4rem;cursor:default}.style-feeds__gallery-content>p:first-child{font-weight:700;font-size:1rem;text-transform:uppercase;letter-spacing:0.1rem;padding-bottom:1rem}.style-feeds__gallery-content>p:last-child{font-family:var(--font-serif);font-size:2.4rem;font-weight:700}

.section-first{margin-top:10rem}

.Best-selling{margin-top:var(--section-margin-top);margin-bottom:var(--section-margin-top)}.Best-selling__gallery{display:grid;grid-template-columns:repeat(3, 1fr);grid-auto-rows:auto}.Best-selling__gallery-box{padding:0 2.5rem;margin-bottom:5rem}.Best-selling__gallery-box:first-child{padding:0 2.5 0 0;grid-column:1 / 3}.Best-selling__gallery-box:nth-child(7){grid-column:2 / 4}.Best-selling__gallery-box:nth-child(2),.Best-selling__gallery-box:nth-child(5),.Best-selling__gallery-box:nth-child(7),.Best-selling__gallery-box:nth-child(10){margin-right:0}.Best-selling__gallery-box:first-child>div:last-child{width:100%;text-align:left;position:relative}.Best-selling__gallery-box:first-child>div:last-child>p:last-child{position:absolute;top:0;right:0}.Best-selling__gallery-box:nth-child(7)>div:last-child{width:100%;text-align:left;position:relative}.Best-selling__gallery-box:nth-child(7)>div:last-child>p:last-child{position:absolute;top:0;right:0}.Best-selling__gallery-photo-box{width:100%;height:45rem;overflow:hidden}.Best-selling__gallery-photo-box>img{width:100%;height:auto}.Best-selling__gallery-content-box{text-align:center;width:60%;margin:4rem auto}.Best-selling__gallery-content-box>p:first-child{font-weight:700;font-size:1rem;text-transform:uppercase;letter-spacing:0.1rem;padding-bottom:1rem}.Best-selling__gallery-content-box>p:nth-child(2){font-family:var(--font-serif);font-size:2.4rem;font-weight:700;padding-bottom:2rem}.Best-selling__gallery-content-box>p:last-child{font-size:1.2rem;font-weight:700}.Best-selling__btn{text-align:center}

.Product-des{height:100%;margin-bottom:var(--section-margin-top)}.Product-des__inner{height:100%;display:flex;justify-content:space-between}.Product-des__content{flex:1 0 65%;display:flex;justify-content:space-between;align-items:center}.Product-des__content-left{width:60%;padding-left:16%}.Product-des__content-left .Product-des__content-left-heading>span:first-of-type{font-size:1.2rem;font-weight:700;margin-bottom:1rem;display:inherit}.Product-des__content-left .Product-des__content-left-heading>span:last-of-type{display:inherit;font-weight:500;text-transform:capitalize;font-size:1.2rem;margin-top:1rem}.Product-des__content-left .Product-des__content-left-heading>h3{font-family:'Cirka';font-size:5rem;text-transform:capitalize}.Product-des__content-left .Product-des__line{display:inline-block;width:50%;border-bottom:1px solid var(--dark-clr);margin:5rem 0rem}.Product-des__content-left .Product-des__content-left-bottom .Product-des__content-left-para{color:var(--font-clr-lite);font-size:var(--font-15);font-weight:400;word-spacing:2px;line-height:2.3rem;padding-bottom:5rem}.Product-des__content-left .Product-des__content-left-bottom .Product-des__sizes{width:60%;margin-bottom:6rem;cursor:default;display:flex;justify-content:space-between}.Product-des__content-left .Product-des__content-left-bottom .Product-des__sizes>div{width:3.5rem;height:3.5rem;border-radius:50px;font-weight:700;font-size:1.2rem;color:var(--font-clr-lite);background-color:var(--main-clr);display:flex;justify-content:center;align-items:center;transition:all 0.1s ease-in}.Product-des__content-left .Product-des__content-left-bottom .Product-des__sizes>div:hover{color:var(--font-clr-lite);background-color:#f0f0f0}.Product-des__content-left .Product-des__content-left-bottom .Product-des__sizes>div.active{font-weight:600;color:white;background-color:var(--dark-clr)}.Product-des__content-right{padding-right:7rem}.Product-des__content-right .Product-des__content-right-photo-box{width:8rem}.Product-des__content-right .Product-des__content-right-photo-box:not(:last-child){padding-bottom:3rem}.Product-des__content-right .Product-des__content-right-photo-box>img{width:100%}.Product-des__big-photo{flex:1 1 35%;height:65rem;position:relative;overflow:hidden}.Product-des__big-photo>div{position:absolute;top:0;left:0;width:100%;opacity:0;transition:opacity 0.3s linear}.Product-des__big-photo>div img{width:100%;height:auto}.Product-des__big-photo>div.active{opacity:1}

.cart{margin-top:var(--section-margin-top)}.cart__checkout-summery{width:30%;margin-left:calc(100% - 30%);margin-bottom:4rem}.cart__checkout-summery>div{display:flex;justify-content:space-between;align-items:center;padding:1.5rem 0rem;border-bottom:1px solid #ececec;text-transform:capitalize;font-weight:500}.cart__checkout-summery>div p{font-size:1.2rem;color:var(--font-clr-lite)}.cart__checkout-summery>div p:last-child{font-weight:700;color:var(--font-clr)}.cart__checkout-action{display:flex;justify-content:space-between;align-items:center;margin-bottom:10rem}.cart-items{list-style:none}.cart-items__header{display:flex;justify-content:space-around;font-size:1.2rem;font-weight:700;text-transform:uppercase;margin-bottom:5rem}.cart-items__header>div:not(:nth-child(1)){text-align:center}.cart-items__header>div{flex:1 1}.cart-items__header>div:nth-child(2){text-align:start;flex:3 1}.cart-items__header>div:not(:last-child){margin-right:.5rem}

.cart-item{display:flex;justify-content:space-around;align-items:center;margin:3rem 0rem;border-bottom:1px solid #ececec;padding-bottom:3rem}.cart-item__item{flex:1 1;font-weight:500;font-size:1.4rem;text-transform:capitalize;text-align:center}.cart-item__photo{text-align:center}.cart-item__photo>div{width:40%}.cart-item__photo>div>img{width:100%}.cart-item__summery{flex:3 1;text-align:start}.cart-item__summery>h4{padding-bottom:1rem}.cart-item__summery>span{font-size:1.2rem;color:var(--font-clr-lite)}.cart-item__item-action{font-size:2rem}

form{margin:10rem 0rem;width:40%}label,input{display:block}label{margin-bottom:1rem;font-size:2rem}input{width:100%;margin-bottom:3rem;padding:1rem}input:focus{border:2px solid #dfdfdf}.login{display:flex;flex-direction:column;align-items:center}button[type="submit"]{padding:.6rem 1rem}

